
.main-title {
  .subtitle p {
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 5px;
  }

  .title {
    .name {
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      letter-spacing: 0.8px;
    }
  }
}

.main {
  position: relative;
  height: 752px;
  @media (max-width: 1023.98px) {
    height: auto;
    padding-bottom: 120px;
  }
  @media (max-width: 767.98px) {
    padding-bottom: 42px;
  }

  h1 {
    font-weight: 600;
    margin-bottom: 10px;

    span {
      font-weight: 700;
    }
  }

  .subtitle {
    width: 600px;
    letter-spacing: 0.6px;
    @media (max-width: 1023.98px) {
      padding: 0 55px 0 0;
    }
    @media (max-width: 767.98px) {
      width: 100%;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      padding: 0;
    }
  }

  &__price {
    margin-top: 55px;
    margin-bottom: 94px;
    display: flex;
    align-items: center;
    @media (max-width: 1023.98px) {
      margin-bottom: 55px;
    }
    @media (max-width: 767.98px) {
      margin-top: 24px;
      margin-bottom: 40px;
    }

    svg {
      width: 46px;
      height: 46px;
      @media (max-width: 767.98px) {
        width: 24px;
        height: 24px;
      }
    }

    p {
      margin-left: 14px;
      font-size: 22px;
      font-style: normal;
      font-weight: 700;
      line-height: 32px;
      @media (max-width: 767.98px) {
        margin-left: 9px;
        font-size: 14px;
      }

      span {
        font-size: 30px;
        font-style: normal;
        font-weight: 700;
        line-height: 32px;
        @media (max-width: 767.98px) {
          font-size: 18px;
        }
      }
    }
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @media (max-width: 767.98px) {
      overflow: hidden;
    }
    picture {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      @media (max-width: 576.98px) {
        width: 768px;
        left: -140px;
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    position: relative;
    padding-top: 203px;
    padding-left: 6px;
    @media (max-width: 1023.98px) {
      padding-top: 120px;
    }
    @media (max-width: 767.98px) {
      padding-left: 0;
      padding-top: 102px;
      max-width: 350px;
    }
  }

  &__popup {
    width: 390px;
    @media (max-width: 480.98px) {
      max-width: 390px;
      width: 100%;
      span {
        font-size: 16px;
        font-weight: 400;
        line-height: 32px;
      }
    }
  }

  @media (max-width: 4500.98px) and (min-width: 1199.98px) {
    &:hover {
      span {

      }
    }
  }
}


//   why-choose   //

