        height: 56px;
      }

      .number {
        position: absolute;
        top: 24px;
        left: 50%;
        color: #0B0B0B;
        text-align: center;
        font-size: 18px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        display: flex;
        justify-content: center;
        align-items: center;
        transform: translate(-50%, -50%);
      }
    }

    .work__item:first-child {
      order: 1;
    }

    .work__item:nth-child(2) {
      order: 3;
    }

    .work__item:nth-child(3) {
      order: 5;
    }

    .work__item:nth-child(4) {
      order: 2;
    }

    .work__item:nth-child(5) {
      order: 4;
    }
  }
  .work__item-top.work__item-top-last {

    .work__item {
      position: relative;
      min-height: 75px;
      padding-left: 90px;
      @media (max-width: 767px) {
        min-height: 75px;
        padding-bottom: 25px;
      }
      @media (max-width: 480px) {
        min-height: 100px;
        padding-bottom: 25px;
      }

      &:first-child {
        &:before {
          position: absolute;
          top: 0;
          left: 70px;
          content: '';
          width: 2px;
          height: 100%;
          background: rgba(108, 43, 237, 1);
        }
      }

      &:nth-child(2) {
        &:before {
          position: absolute;
          top: 0;
          left: 70px;
          content: '';
          width: 2px;
          height: 100%;
          background: rgba(176, 47, 246, 1);
        }
      }

      &:nth-child(3) {
        &:before {
          position: absolute;
          top: 0;
          left: 70px;
          content: '';
          width: 2px;
          height: 100%;
          background: rgba(236, 51, 254, 1)
        }
      }

      &:nth-child(4) {
        &:before {
          position: absolute;
          top: 0;
          left: 70px;
          content: '';
          width: 2px;
          height: 100%;
          background: rgba(146, 45, 243, 1);
        }
      }

      &:nth-child(5) {
        &:before {
          position: absolute;
          top: 0;
          left: 70px;
          content: '';
          width: 2px;
          height: 100%;
          background: rgba(208, 49, 251, 1);
        }
      }
    }
  }

  .work__item-bottom.work__item-bottom-last {
    display: none;
  }
  .work__item-top.work__item-top-last .work__item:first-child {
    order: 5;
  }
  .work__item-top.work__item-top-last .work__item:nth-child(2) {
    order: 3;
  }
  .work__item-top.work__item-top-last .work__item:nth-child(3) {
    order: 1;
  }
  .work__item-top.work__item-top-last .work__item:nth-child(4) {
    order: 4;
  }
  .work__item-top.work__item-top-last .work__item:nth-child(5) {
    order: 2;
  }
}

