//   our-clients //

.our-clients {
  padding-top: 120px;
  background: #FFF;
  overflow: hidden;
  padding-bottom: 100px;
  @media (max-width: 1023.98px) {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  @media (max-width: 767.98px) {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  &__slider {
    position: relative;
    margin-top: -10px;
    @media (max-width: 767.98px) {
      margin: 0 20px;
    }
  }

  .gallery__navigation {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    justify-content: space-between;
    width: 100%;
    @media (max-width: 767.98px) {
      position: relative;
      text-outline: inherit;
      transform: inherit;
      justify-content: center;
    }
    ._prev {
      margin-left: -68px;
      z-index: 1;
      @media (max-width: 1440.98px) {
        margin-left: -20px;
      }
      @media (max-width: 767.98px) {
        margin: 0 15px;
      }
      &:hover {
        cursor: pointer;
      }
    }

    ._next {
      margin-right: -68px;
      z-index: 1;
      @media (max-width: 1440.98px) {
        margin-right: -20px;
      }
      @media (max-width: 767.98px) {
        margin: 0 15px;
      }
      &:hover {
        cursor: pointer;
      }
    }
  }

  .clients__item {
    width: 100%;
    height: 263px;
    padding: 10px;
    @media (max-width: 767px) {
      width: 100%;
      height: auto;
      padding: 0;
    }
  }
  .clients__photo{
    transition: 0.5s;
    @media (max-width: 4500.98px) and (min-width: 1199.98px) {
      &:hover {
        border-radius: 6px;
        background: #FFF;
        filter: drop-shadow(0px 0px 15px rgba(8, 2, 60, 0.10));
        cursor: pointer;
      }
    }
  }

  &__clients__photo {
    transition: 0.5s;
  }
  .swiper-wrapper{
    padding:20px 0;
  }

}


