//   faq    //


.faq {
  padding-top: 110px;
  background: #F6F6F8;
  padding-bottom: 85px;
  @media (max-width: 1023.98px) {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  @media (max-width: 767.98px) {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  .main-title .title p {
    letter-spacing: 0;
  }
}

.accordion {
  margin-top: 125px;
  @media (max-width: 1023.98px) {
    margin-top: 50px;
  }
}

.accordion__item {
  border-radius: 10px;
  background: #FFF;
  padding: 28px 40px 28px 72px;
  margin-bottom: 24px;
}

.question {
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  cursor: pointer;
  position: relative;
  @media (max-width: 4500.98px) and (min-width: 1199.98px) {
    &:hover {
      &:after {
        background-color: #471AAB;
      }
    }
  }

  &:after {
    content: "";
    position: absolute;
    background-image: url("/assets/images/main/block11/plus.svg");
    background-position: center;
    background-size: 16px;
    transition: 0.5s;
    left: -46px;
    top: -5px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #7339F5;
    background-repeat: no-repeat;
    transition: 0.5s;

  }
}

.question.active {
  @media (max-width: 4500.98px) and (min-width: 1199.98px) {
    &:hover {
      &:after {
        background-color: #471AAB;
      }
    }
  }
}

.question.active::after {
  content: "";
  position: absolute;
  background-image: url("/assets/images/main/block11/minus.svg");
  background-position: center;
  background-size: 16px;
  transition: 0.5s;
  left: -46px;
  top: -5px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #7339F5;
  background-repeat: no-repeat;

}

.answercont {
  max-height: 0;
  overflow: hidden;
  transition: 0.3s;
}

.answer {
  padding: 23px 0 0;

  p {
    color: #35363A;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
  }
}

